import AngleRightIcon from '@/assets/icons/angle_right.svg';
import AngleDownIcon from '@/assets/icons/angle_down.svg';
export default {
  data: () => {
    return {
      show: false
    };
  },
  components: {
    AngleRightIcon,
    AngleDownIcon
  },
  methods: {
    toggle() {
      this.show = !this.show;
    },
    beforeEnter(el) {
      el.style.height = '0';
    },
    enter(el) {
      el.style.height = el.scrollHeight + 'px';
    },
    beforeLeave(el) {
      el.style.height = el.scrollHeight + 'px';
    },
    leave(el) {
      el.style.height = '0';
    }
  }
};